import { useState, useEffect } from 'react';
import Router from 'next/router';

import { isLocal } from '../constants/constants';
import { getArticleMap, getBlog, getDate, getRecentArticles } from './utils';

export default function useBlogLoader({
    id,
}) {
    const [blog, setBlog] = useState({
        title: '',
        description: '',
        categoryId: '',
        readTimeMin: 0,
        date: getDate(),
        content: [],
    });
    const [recent, setRecent] = useState([]);

    useEffect(() => {
        if (!id) return;
        let blogId = id;
        if (isLocal) {
            getArticleMap()
                .then((data) => {
                    blogId = data.articleNameMap[id];
                    getBlog(blogId)
                        .then((res) => {
                            if (res) {
                                const data = res.data && res.data.data ? res.data.data : (res.data ? res.data : {});
                                setBlog({
                                    content: data.content && data.content.length ? data.content : [],
                                    title: data.title,
                                    description: data.description,
                                    date: getDate(data.date),
                                    readTimeMin: data.readTimeMin,
                                    categoryId: data.categoryId,
                                });
                            }
                            window.scrollTo(0, 0);
                        })
                        .catch(() => {
                            Router.push('/', '/', { shallow: false });
                        });
                })
                .catch(() => {
                });

        } else {
            getBlog(id)
                .then((res) => {
                    if (res) {
                        const data = res.data && res.data.data ? res.data.data : (res.data ? res.data : {});
                        setBlog({
                            content: data.content && data.content.length ? data.content : [],
                            title: data.title,
                            description: data.description,
                            date: getDate(data.date),
                            readTimeMin: data.readTimeMin,
                            categoryId: data.categoryId,
                        });
                    }
                    window.scrollTo(0, 0);
                })
                .catch(() => {
                    Router.push('/', '/', { shallow: false });
                });
        }
    }, [id]);

    useEffect(() => {
        getRecentArticles()
            .then((res) => {
                if (res) setRecent(res.data && res.data.length ? res.data : []);
            })
            .catch(() => {
            });
    }, []);

    return {
        blog,
        recent,
    };
}